import _ from "lodash";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { fetchDataFinished, fetchDataStart } from "redux/actions";
import { getInforCustomByToken } from "services/EnsurranceService";
import './Ensurrance.scss'

function Ensurrance() {
    const dispatch = useDispatch();
    const [token, setToken] = useState('');
    const [result, setResult] = useState({});
    const [isShowResult, setIsShowResult] = useState(false);
    const handleOnClickCheckBtn = async (token) => {
        if (token) {
            dispatch(fetchDataStart());
            let res = await getInforCustomByToken(token);
            if (res && res.errCode === 0) {

                setResult(res.data);
                setToken('');
                setIsShowResult(true);
            }
            dispatch(fetchDataFinished());
        }
    }
    const handleOnChangeInput = (e) => {
        setToken(e.target.value)
    }
    const enterPressed = (event) => {
        var code = event.keyCode || event.which;
        if (code === 13) {
            // handleOnClickSearchBtn();
            handleOnClickCheckBtn(token);

        }
    }
    return (
        <div className="wrap_ensurrance">


            <div className="container_ensurrance">
                <h2 className="title_ensurrance">KIM TRA BO HNH SN PHM</h2>
                <div className="search_ensurrance">
                    <input value={token} onChange={e => handleOnChangeInput(e)} className="input_ensurrance" placeholder="Nhp Serial ti y" onKeyPress={enterPressed.bind(this)} />
                    <button onClick={() => handleOnClickCheckBtn(token)} className="btn_ensurrance">Kim tra</button>
                </div>
                {
                    isShowResult &&
                    <>
                        {
                            !_.isEmpty(result) ?
                                <div className="result_ensurrance">
                                    <ul className="result_ensurrance-list">
                                        <li className="result_ensurrance-item">Tn khch hng: {result.name}</li>
                                        <li className="result_ensurrance-item">a ch email: {result.email}</li>
                                        <li className="result_ensurrance-item">S in thoi : {result.phoneNumber}</li>
                                        <li className="result_ensurrance-item">Ngy mua: {result.purchaseDate}</li>
                                        <li className="result_ensurrance-item">Thi hn bo hnh: {result.warrantyPeriod}</li>
                                        <li className="result_ensurrance-item">Ngy ht hn: {result.expiredTime}</li>
                                        <li className="result_ensurrance-item">Sn phm  mua: {result.purchasedProduct}</li>

                                    </ul>
                                </div> :
                                <div className="result_ensurrance">
                                    <h3>M khng tn ti</h3>
                                </div>
                        }
                    </>
                }
            </div>
        </div>
    );
}

export default Ensurrance;