import { useNavigate } from 'react-router-dom';
import './CardItem.scss'
function CardItem(props) {
    let navigate = useNavigate();

    const handleChangePage = () => {
        navigate(`../product/${props.id}`);
    }
    console.log(props)

    return (
        <div className="card-wrapper" onClick={handleChangePage}>
            <img src={props.image} placeholder="Laptop" className="card-img" />
            <h3 className="card-name">{props.name}</h3>
            <span className="card-price">{props.price ? `${props.price} VN` : 'ang cp nht'}</span>
            <div className="containerDetailConfig">
                <span className="itemDetailConfig">{props.CPUType ? `${props.CPUType} ` : 'ang cp nht'}/ </span>
                <span className="itemDetailConfig">{props.ramMemory ? `${props.ramMemory} ` : 'ang cp nht'}/ </span>
                <span className="itemDetailConfig">{props.memory ? `${props.memory} ` : 'ang cp nht'}/ </span>
                <span className="itemDetailConfig"> {props.sizeScreen ? `${props.sizeScreen} ` : 'ang cp nht'}</span>
            </div>
            {props.discount > 0 && <button className='discount'>Gim  {props.discount && `${props.discount}% `}</button>}


        </div>
    )
}

export default CardItem;